body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.delete-btn {
  margin-left: 20px;
  display: inline-flex;
  border: none;
  color: #4CAF50;
  text-decoration: none;
  font-size: 1em;
  cursor: pointer;
}

.list-overflow-container {
  max-height: 545px;
  border: 1px #DDDDDD solid;
  border-radius: 4px;
  overflow-y: scroll;
  overflow-x: hidden;
}

.list-overflow-container ul {
  margin: 0;
}

.list-overflow-container li {
  border-radius: 0px;
  border-left: none;
  border-right: none;
}

.list-overflow-container li * {
  cursor: pointer;
}

.list-overflow-container li a {
  color: #2A878C;
}

.list-overflow-container li:first-child {
  border-top: none;
}

.list-overflow-container li:last-child {
  border-bottom: none;
}

